import React, { useState } from "react";
import { FloatingActionButton } from "../../components";
import DelayLink from 'react-delay-link';

const Launcher = (props) => {
  let { title, className = "", subtitle, logo } = props;
  const [start, setStart] = useState(false);

  return (
    <div className={"launcher " + className + (!!start ? " is-started" : "")}>
      <LauncherBackground start={start} shape="squares" />
      <div className="launcher-content">
        {!!logo && <img className="launcher-logo" src={logo} alt="dsk_logo"></img>}
        <h2 className="launcher-title">{title}{!!subtitle && <small className="launcher-subtitle">{subtitle}</small>}</h2>
        <LauncherTextAnimation />
      </div>
      <div className="launcher-action">
        <DelayLink delay={9000} to="/welcome" clickAction={() => setStart(true)} replace={false}>
          <FloatingActionButton icon="play_arrow" className="launcher-button center"/>
        </DelayLink>
      </div>
    </div>
  );
};

const LauncherTextAnimation = props => {
  return (
    <div className="launcher-text-animation">
      <span>Dream</span>
      <span>Design</span>
      <span>Develop</span>
    </div>
  )
}

const LauncherBackground = props => {
  let { start, shape } = props;
  return (
    <div className={"launcher-background " + (!!start ? " is-started" : "")}>
      {(shape === "squares" &&
        <ul className="squares">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      )}

      {(shape === "circles" &&
        <ul className="circles">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      )}

      {(shape === "rings" &&
        <ul className="rings">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      )}
    </div>
  )
}

export default Launcher;
