import React, { Fragment } from "react";
import { BackToHub } from "./components";
import { Icon, Launcher } from "components";

const AppBar = props => {
    let { logo, icon } = props;

    return (
        <Fragment>
            <header className="app-bar">
                {!!logo && <img src={logo} alt="app-logo" className="app-bar-logo" />}
                {!!icon && <Icon name="code" className="icon-36" />}
            </header>
        </Fragment>
    );
};

export default AppBar;
