import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { ScrollToTop, AppBar } from "components";
import * as views from "./views";

function App() {
  return (
    <div id="app">
      <Router>
        <ScrollToTop />
        <AppBar icon="heart" />
        <Switch>
          <Route exact path="/" component={views.Home} />
          <Route exact path="/welcome" component={views.Welcome} />
          <Route exact path="/hub" component={views.Hub} />
          <Route exact path="/article" component={views.Article} />
          <Route exact path="/profile" component={views.Profile} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
